.sun{
    -webkit-animation: sunMove 120s linear infinite;
            animation: sunMove 120s linear infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}

@-webkit-keyframes sunMove {
    0% {
        left:10%;
        top: 50%;
    }
    50%{
        left: 50%;
        top: 10%;
    }
    100%{
        left:90%;
        top: 50%;

    }
}

@keyframes sunMove {
    0% {
        left:10%;
        top: 50%;
    }
    50%{
        left: 50%;
        top: 10%;
    }
    100%{
        left:90%;
        top: 50%;

    }
}


@-webkit-keyframes sunMoveLow {
    0% {
        left:10%;
        top: 80%;
    }
    50%{
        left: 50%;
        top: 50%;
    }
    100%{
        left:90%;
        top: 80%;

    }
}


@keyframes sunMoveLow {
    0% {
        left:10%;
        top: 80%;
    }
    50%{
        left: 50%;
        top: 50%;
    }
    100%{
        left:90%;
        top: 80%;

    }
}

@media screen and (max-width: 600px){
    .sun{
        -webkit-animation: sunMoveLow 120s linear infinite;
                animation: sunMoveLow 120s linear infinite;
    }
}


