.App{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Quicksand', sans-serif;
  height: 100vh;
  overflow: hidden;
}

h1 {
  font-family: 'Amatic SC', cursive;
  font-size: 5rem;
  font-weight: 400;
  margin-bottom: 20px;
}

h1, h2, h3{
  margin: auto;
  text-align: center;
}

.twitchWindow {
  margin:auto;
  -webkit-box-shadow: 0px 0px 6px 2px rgba(0,0,0,0.6);
          box-shadow: 0px 0px 6px 2px rgba(0,0,0,0.6);
  width:100%;
  height:100vh;
  -webkit-animation: crossfade 4s forwards;
          animation: crossfade 4s forwards;
}

.live{
  height: 0;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

@-webkit-keyframes hide {
  from{
    visibility: visible;
    opacity: 1;
    height:100px
  }
  to{
    visibility: collapse;
    opacity: 0;
    height:0px;
  }
}

@keyframes hide {
  from{
    visibility: visible;
    opacity: 1;
    height:100px
  }
  to{
    visibility: collapse;
    opacity: 0;
    height:0px;
  }
}

@-webkit-keyframes theatre {
  from{
    background-color: rgba(0,0,0,0);
  }
  to{
    background-color: rgba(0,0,0,1);
  }
}

@keyframes theatre {
  from{
    background-color: rgba(0,0,0,0);
  }
  to{
    background-color: rgba(0,0,0,1);
  }
}

@-webkit-keyframes crossfade {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@keyframes crossfade {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}


